import { Plugin } from '@ali/4ever-cangjie';
import { BiPluginConfig } from '../../types';
import createCangjieInput from './createCangjieInput';
import createOnKeydown from './createOnKeydown';

function createAutoSpacingPlugin (config: BiPluginConfig): Plugin {
  return {
    onCangjieInput: createCangjieInput(config),
    onKeyDown: createOnKeydown(config),
  }
};

export default createAutoSpacingPlugin;
